.info {
  color: #15151a;
  background-color: #fff;
  padding: 16px;
  font-family: Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.hidden {
  display: none;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin: 20px;
  padding: 10px 20px;
}

.gallery {
  grid-gap: 20px;
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin: 20px 0;
  display: grid;
}

.gallery img {
  width: 100%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  display: block;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, .2);
}

.search-form {
  justify-content: center;
  align-items: center;
  padding: .5%;
  display: flex;
}

.search-form input {
  border: 2px solid #efe4f8;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  box-shadow: 1px 1px 3px rgba(67, 64, 241, .2);
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

